import React, { useState, useEffect, useRef } from 'react';
import styled from 'styled-components';
import {darken, lighten} from 'polished';

import VisibilitySensor from 'react-visibility-sensor';

interface PercentBarProps {
    label: string;
    percent: number;
    color: string;
    onClick: any;
    active: boolean;
}

const PercentBarDiv = styled.div<PercentBarProps>`
    display: flex;
    cursor: pointer;

    .label {
        width: 120px;
        font-weight: bold;
    }

    .bar {
        display: flex;
        flex: 1 0 auto;
    }

    .filled {
        background-color: ${props => props.color};
        margin-right: 6px;
        border: 3px solid transparent;

        &:hover {
            box-shadow: 3px 3px 4px 0px ${props => darken(0.1, props.color)};
        }
    }

    .empty {
        text-align: right;
        flex: 1 1 auto;
    }

    &.active .filled {
        box-shadow: 3px 3px 4px 0px ${props => darken(0.1, props.color)};

    }
`;

function PercentBar (props: PercentBarProps) {

    const [percentage, setPercentage] = useState(0),
          [elementVisible, setElementVisible] = useState<boolean>(false),
          currentPercentage = useRef(percentage);

    function onVisible(isVisible: boolean) {
        if (isVisible && !elementVisible) {
            setElementVisible(true);
        }
    }

    useEffect(() => {
        if(elementVisible && currentPercentage.current === 0) {
            const interval = setInterval(() => {
                setPercentage(oldPercent => {
                    if (oldPercent >= props.percent) {
                        clearInterval(interval);
                        return oldPercent
                    } else {
                        return oldPercent + 1
                    }
                });
            }, 10);
        }
    }, [elementVisible, currentPercentage, props.percent]);

    return <PercentBarDiv className={props.active ? 'active' : ''} {...props}>
        <div className='label'>
            {props.label}
        </div>
        <VisibilitySensor onChange={onVisible}>
            <div className='bar'>
                <div className='filled' style={{width: `${percentage}%`}}/>
                <div className='empty'>
                    {percentage}%
                </div>
            </div>
        </VisibilitySensor>
    </PercentBarDiv>
}

export default PercentBar;