import React from 'react';

import HeroSection from './sections/heroSection';
import SkillSection from './sections/skillSection';
import { Section, Container } from '../layout';

function HomePage() {
    return <>
        <HeroSection style={{
      justifyContent: 'center',
      alignItems: 'center',
      color: 'white',
      backgroundImage: 'url(' + require("../assets/DSC05102.JPG") + ')',
      backgroundSize: 'cover',
      backgroundPosition: 'center'
      
    }}>
      <div style={{
          width: '100%',
          backgroundColor: '#ffffff61',
          padding: '32px',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          flexFlow: 'column nowrap',
          }}>
          <div>
        
          <h1 style={{fontWeight: 800, fontSize: '6rem'}}>
            BENJAMIN BROMLEY
          </h1>

          <h2 className="subheading textRight">
            <i>Software Engineer</i>
          </h2>
        </div>
      </div>
    </HeroSection>

    <Section style={{
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      flexFlow: 'column nowrap'
    }}>
      <Container>
        <h2 className="textCenter">MY SKILLS</h2>
        <SkillSection/>
      </Container>
    </Section>

    {/* <Section style={{
      backgroundColor: '#8E8268'
      // display: 'flex',
      // justifyContent: 'center',
      // alignItems: 'center',
      // flexFlow: 'column nowrap'
    }}>
      <Container>
        <h1 className="textCenter" style={{color: 'white'}}>CONTACT ME</h1>
        <ContactForm/>
      </Container>
    </Section> */}

    <Container>
      <h1 className="textCenter">
        TO BE CONTINUED...
      </h1>
    </Container>
    </> 
}

export default HomePage;