import React from 'react';
import { Section, Tint } from '../../layout';

function HeroSection(props: any) {
    return <Section style={props.style}>
      <Tint 
        style={{
          display: 'flex',
          alignItems: 'center',
          
        }}
        color="#86b3d15e">
          {props.children}
      </Tint>
    </Section>
}

export default HeroSection;